import { Component, OnInit } from '@angular/core';
import {SocketServices} from '../services/socket.io.service';
import {FormsModule} from '@angular/forms';
import { Router } from '@angular/router';
import { concat } from 'rxjs';
import {ImageuploadService} from '../services/imageupload.service'

const SERVER_URL = 'http://localhost:3000';
@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  styleUrls: ['./chat.component.css']
})
export class ChatComponent implements OnInit {
  userName = sessionStorage.getItem("username");
  userImage = sessionStorage.getItem("userimage");
  messagecontent:string = "";
  messages:string[] = [];
  ioConnection:any; 
  rooms = [];
  roomslist:string = "";
  isinRoom=false;
  currentroom:string = "";
  //what the user can do
  Powers = "";
  PowersText = "";

  channels = [];
  Channellist:string = "";
  currentChannel:string = "";


  //histroy 
  chatHistory = [];
  
  //chat images
  title = 'app';
  selectedfile = null;
  imagepath="";

  constructor(private imguploadService: ImageuploadService, private socketservice:SocketServices, private router: Router) { }

  private initIOConnection(){
    this.socketservice.initSocket();
    this.ioConnection = this.socketservice.onMessage()
      .subscribe((message:string)=> {
        this.messages.push(message);
      });
  }

  ngOnInit(){
    this.initIOConnection();
    this.socketservice.reqroomlist(this.Channellist, this.userName);
    this.socketservice.getroomList((msg)=>{this.rooms = JSON.parse(msg)});
    this.socketservice.reqChannelList(this.userName);
    this.socketservice.getChannelList((msg)=>{this.channels = (msg)});
    this.socketservice.joined((msg)=>{ this.currentroom = msg
      if(this.currentroom != ""){
        this.isinRoom = true;
      }else{
        this.isinRoom = false;
      }
      });
    this.Powers = sessionStorage.getItem("power");
  }

  joinroom(){
    this.socketservice.joinroom(this.roomslist);
    this.isinRoom = true;
    console.log("joing   " + this.roomslist);
    this.socketservice.reqChatHis();
    this.socketservice.getChatHis((msg) =>{this.chatHistory = msg});
  }

  leaveroom(){
    this.socketservice.leaveroom(this.currentroom);
    this.roomslist = null;
    this.currentroom = "";
    this.isinRoom = false;
    this.messages = [];
  }

  public chat(){
    if(this.messagecontent){

      //make packet and send add user to message send 
      var packet = [];
      packet.push(this.userName);
      packet.push(this.messagecontent);
      packet.push(this.userImage); 
      packet.push("no");
      this.socketservice.send(packet);
      this.messagecontent = null;
    }
    else{
      console.log("no message");
    }
  }

  UserSettings(){
    alert("user settings");
    this.router.navigate(['/user-settings']); 
  }

  //logout functions
  LogOut(){
    sessionStorage.clear();
    this.router.navigate(['/home']);
  }

  UpdateLists(){
    console.log(this.Channellist);
    this.socketservice.reqroomlist(this.Channellist, this.userName);
    this.socketservice.getroomList((msg)=>{this.rooms = JSON.parse(msg)});
    this.socketservice.reqChannelList(this.userName);
    this.socketservice.getChannelList((msg)=>{this.channels = (msg)});
  }
  onFileSelected(event){
    console.log(event);
    this.selectedfile = event.target.files[0];
  }

  onUpload(){
    // set user image to user 
    const fd = new FormData();
      //make packet and send add user to message send 
      var packet = [];
      packet.push(this.userName);
      packet.push(this.selectedfile.name);
      packet.push(this.userImage); 
      packet.push("yes");
      this.socketservice.send(packet);
      this.messagecontent = null;
    fd.append('image', this.selectedfile, this.selectedfile.name);
    this.imguploadService.imgupload(fd).subscribe(res=>{
      this.imagepath = res.data.filename;
      console.log(res.data.filename + ',' + res.data.size);
      //set mongo db 
    });
  }

}

