import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import {SocketServices} from '../services/socket.io.service';
import {SuperUserService} from '../services/super-user.service';

const SERVER_URL = 'http://localhost:3000';

@Component({
  selector: 'app-user-setting',
  templateUrl: './user-setting.component.html',
  styleUrls: ['./user-setting.component.css']
})
export class UserSettingComponent implements OnInit {
  message = "";
  username = sessionStorage.getItem("username");

  clickCounter = 0;
  UserPower = sessionStorage.getItem("power");
  SuperUSER = false;
  GroupAdmin = false;
  GroupAss = false;
  constructor(private router: Router, private sockets: SuperUserService) { }

  ngOnInit(): void {
    this.sockets.initSocket();
    if(this.UserPower == "3"){
      this.SuperUSER = true;
      this.GroupAdmin = true;
      this.GroupAss = false;
    }
    else if(this.UserPower == "2"){
      this.GroupAdmin = true;
      this.GroupAss = false;
    }
    else if(this.UserPower == "1"){
      this.GroupAss = true;
    }
  }

  BackToChat(){
    this.router.navigate(['/chat']); 
  }

  ImageUpload(){
    this.router.navigate(['/image']);
  }

  check(){
    
  }

  //Super User Varibles 
  userID = "";
  password = "";
  power = "";
  operation = "";
  
  CreateUserClick(){
    this.sockets.CreateUser(this.userID, this.password, this.power);
    this.sockets.GetSuccuss((msg)=>{this.operation = msg
      if(this.operation != "Fail"){
        alert("success");
      }
      else{
        alert("fail");
      }

    });
  }

  addclick(){
    this.sockets.AdminUserSet(this.userID);
    this.sockets.GetSuccuss((msg)=>{this.operation = msg
      if(this.operation != "Fail"){
        alert("success");
      }
      else{
        alert("fail");
      }

    });
  }
  
  removeClicked(){
    this.sockets.RemoveUser(this.userID);
  }

  SUPERCLICKED(){
    this.sockets.SuperUserSet(this.userID);
    this.sockets.GetSuccuss((msg)=>{this.operation = msg
      if(this.operation != "Fail"){
        alert("success");
      }
      else{
        alert("fail");
      }

    });
  }

  //Group admin tools
  GroupID = "";
  Description = "";
  GroupCreateClicked(){
    this.sockets.SpawnGroup(this.GroupID);
    this.sockets.GetSuccuss((msg)=>{this.operation = msg
      if(this.operation != "Fail"){
        alert("success");
      }
      else{
        alert("fail");
      }

    });
  }
  //
  ChannelID = "";
  ChanGroupID = "";
  ChannelCreateClicked(){
    this.sockets.Spawnchannel(this.ChanGroupID, this.ChannelID);
    this.sockets.GetSuccuss((msg)=>{this.operation = msg
      if(this.operation != "Fail"){
        alert("success");
      }
      else{
        alert("fail");
      }

    });
  }

  ChannelUserCreateClicked(){
    alert("user -> channel" + this.ChannelID + this.userID);
    let packet:string[] = [this.ChannelID, this.userID]; 
    this.sockets.UserRoomLink(packet);
  }

  ChannelUserremoveClicked(){
    alert("user -> channel" + this.ChannelID + this.userID);
    let packet:string[] = [this.ChannelID, this.userID]; 
    this.sockets.RemoveRoomLink(packet);
  }
  GroupUserCreateClicked(){
    alert("user -> channel" + this.ChannelID + this.userID);
    let packet:string[] = [this.ChannelID, this.userID]; 
    this.sockets.UserGroupLink(packet);
  }

  GroupUserremoveClicked(){
    alert("user -> channel" + this.ChannelID + this.userID);
    let packet:string[] = [this.ChannelID, this.userID]; 
    this.sockets.RemoveGroupLink(packet);
  }
}
