import { Component, OnInit } from '@angular/core';
import {ImageuploadService} from '../services/imageupload.service'
import {SocketServices} from '../services/socket.io.service'
import { Router } from '@angular/router';
import {Observable} from 'rxjs';

const SERVER_URL = 'http://localhost:3000';
 

@Component({
  selector: 'app-image-upload',
  templateUrl: './image-upload.component.html',
  styleUrls: ['./image-upload.component.css']
})
export class ImageUploadComponent implements OnInit {

  title = 'app';
  selectedfile = null;
  imagepath="";
  userImage = sessionStorage.getItem("userimage");

  constructor(private sockets: SocketServices , private imguploadService: ImageuploadService,  private router: Router) { }

  ngOnInit(): void {
    this.sockets.initSocket();
  }

  onFileSelected(event){
    console.log(event);
    this.selectedfile = event.target.files[0];
  }

  onUpload(){
    // set user image to user 
    const fd = new FormData();
    this.sockets.sendusername(sessionStorage.getItem("username"),  this.selectedfile.name)
    fd.append('image', this.selectedfile, this.selectedfile.name);
    this.imguploadService.imgupload(fd).subscribe(res=>{
      this.imagepath = res.data.filename;
      console.log(res.data.filename + ',' + res.data.size);
      sessionStorage.setItem("userimage", this.selectedfile.name);
      //set mongo db 
    });
  }

  backclicked(){
    this.router.navigate(['/chat']);
  }

}
